import { Component, Input } from '@angular/core';
import { ProjectService } from '../../../services/project.service'

@Component({
  selector: 'app-projects-modal',
  templateUrl: './projects-modal.component.html',
  styleUrls: ['./projects-modal.component.css']
})
export class ProjectsModalComponent {
  @Input() dataValue;
  @Input() selectedValue;
  @Input() title: string;

  itemElement: string;
  project: {};
  public visible = false;
  public dropFlag: boolean = false;
  public changeFlag: boolean = false;
  public createFlag: boolean = false;
  private visibleAnimate = false;

  constructor(private projectService: ProjectService) { }

  public showAddModal(flag): void {
    this.createFlag = flag;
    this.visible = true;
    setTimeout(() => this.visibleAnimate = true, 100);
  }

  public showDropModal(flag, itemProject):void{
    this.dropFlag = flag;
    this.project = itemProject;
    this.visible = true;
    setTimeout(() => this.visibleAnimate = true, 100);
  }

  public showChangeProjectModal(chengeProject, itemProject){
    this.changeFlag = chengeProject;
    this.project = itemProject;
    this.visible = true;
    setTimeout(() => this.visibleAnimate = true, 100);
  }

  public hide(): void {
    this.visibleAnimate = false;
    setTimeout(() => this.visible = false, 300);
    this.dropFlag = false;
    this.changeFlag = false;
    this.createFlag = false;
  }

  public onContainerClicked(event: MouseEvent): void {
    if ((<HTMLElement>event.target).classList.contains('modal')) {
      this.hide();
    }
  }

  public addItem(itenEl: string){
    let project = {};
    project['title'] = itenEl;
    this.projectService.postProject(project).subscribe(data => {
      if(data.success){
        this.dataValue.push(project);
      } else {
      
      }
    });
    this.visible = false;
    this.dropFlag = false;
    this.changeFlag = false;
    this.createFlag = false;
    setTimeout(() => this.visible = false, 300);
    this.itemElement = null;
  }
  
  public changeItem(project: any, projectTitle: string){
    let prj = project;
    let prjName = projectTitle;
    for (var key in this.dataValue) {
      if(this.dataValue[key]._id === project._id){
        this.dataValue[key].title = prjName;
        //console.log(this.dataValue[key].title);
        this.projectService.updateProject(project._id, this.dataValue[key]).subscribe(data => {
          if(data.success){
            console.log(true);
          } else {
            console.log(false);
          }
        })
      }
    }
    this.visible = false;
    this.dropFlag = false;
    this.changeFlag = false;
    this.createFlag = false;
    setTimeout(() => this.visible = false, 300);
    this.itemElement = null;
  }

  public deleteItem(itenEl: any){
    let elementIndex: number = itenEl._id;
    this.projectService.deleteProject(elementIndex).subscribe(data => {
      if(data.success){
        this.dataValue.splice(0,1);
      } else {
        console.log(false);
      }
    });
    this.visible = false;
    this.dropFlag = false;
    this.changeFlag = false;
    this.createFlag = false;
  }

  public close() {
    this.visible = false;
    setTimeout(() => this.visible = false, 300);
    this.dropFlag = false;
    this.changeFlag = false;
    this.createFlag = false;
  }
}



